// DARK THEME

.dark-theme {
  background-color: $color-background;
}



.header__logo--dark {
  display: block;
}

.header__logo--light {
  display: none;
}





.dark-theme .main-nav__link {
  color: $color-default-white;
}

.dark-theme .main-nav__toggle {
  background-image: url(../img/sprite/icon-open-dark.svg);
}

.dark-theme .main-nav__link:hover {
  color: $color-primary;
}

.dark-theme .main-nav .main-nav__item .main-nav__link--active {

  color: $color-primary;
}

.dark-theme .main-nav--opened .main-nav__link {
  color: $color-text-primary;
}


.dark-theme .main-nav--opened .main-nav__toggle {
  top: 9.5px;
  right: 7px;

  background-image: url(../img/sprite/icon-close.svg);
  background-repeat: no-repeat;
}


.dark-theme .header .btn {
  color: $color-text-primary;

  background-color: $color-default-white;
}

.dark-theme .main-nav--opened .btn {
  color: $color-default-white;

  background-color: $color-text-primary;
}




@media screen and (min-width: 769px) {
  .dark-theme .main-nav__fullscreen-sub-menu {
    //color: $color-default-white;

    background-color: $color-background;
  }
}


@media screen and (min-width: 769px) {
  .dark-theme .main-nav__list-first ul,
  .dark-theme .main-nav__list-second ul {
    //color: $color-default-white;

    background-color: $color-background;
  }
}

@media screen and (min-width: 769px) {
  .dark-theme .main-nav .main-nav__sub-sub-menu {
    //color: $color-default-white;

    background-color: $color-background;
  }
}

@media screen and (min-width: 769px) {
  .dark-theme .main-nav .main-nav__sub-sub-item span,
  .dark-theme .main-nav .main-nav__sub-sub-item p,
  .dark-theme .main-nav .main-nav__sub-sub-item div {
    color: $color-default-white;
  }
}

@media screen and (min-width: 769px) {
  .dark-theme .main-nav .main-nav__sub-item a {
    color: $color-default-white;
  }
}
